
* {
  padding: 0;
  margin: 0;
}
.fl{
  float: left;
}
.fr{
  float: right;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clearfix {
  display: inline-block;
}
.clearfix {
  display: block;
}
.lj_chat_wrapper {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index : 100000;
  cursor: pointer;
  font: 14px/1.3 'Arial',sans-serif;
}
.lj_chat_wrapper a{
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-decoration: none;
}
.lj_chat_wrapper textarea{
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
.lj_chat_wrapper h1,h2,h3,h4,h5{
  font-weight: normal;
}
.lj_chat_wrapper ul{
  list-style : none;
}
.lj_window{
  width: 560px;
  position: absolute;
  right: 0px;
  bottom: 0px;
  background: #fff;
  box-shadow: -3px -2px 8px -1px rgba(0,0,0,0.2);
  border-radius: 2px 0px 0px 2px;
  color: #333;
}



