
.lj_dialog_box{
    width: 400px;
    height: 400px;
    background: #f2f2f5;
    overflow: hidden;
}
.lj_dialog_head{
    position:relative;
    height: 38px;
    padding: 0 10px;
    line-height: 38px;
    border-bottom: 1px #d9d9d9 solid;
    background: #fff;
}
.lj_dialog_title{
    max-width: 225px;
    line-height: 38px;
    color: #333;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
    font-size: 16px;
    font-weight: bold;
    overflow: hidden;
}
.lj_dialog_status{
    color: #333;
    font-size: 16px;
    font-weight: bold;
}
.lj_dialog_body{
    position: relative;
    height: 362px;
}
.lj_dialog_cont{
    position: relative;
    top: 0px;
    overflow-y:auto;
    display: block;
    visibility: visible;
    height: 319px;
}
.lj_dialog_more{
    padding-top: 10px;
    text-align: center;
}
.lj_link1{
    color: #eb7350;
    font-size: 12px;
}
.lj_window_close{
    position:absolute;
    right:6%;
    color:#696e78;
    font-size:25px;
    cursor : pointer;
}



